import * as React from "react";
import type { HeadFC, PageProps } from "gatsby";
import {
  TbBrandLinkedin,
  TbBrandInstagram,
  TbBrandTelegram,
  TbBrandStrava,
  TbArrowUpRight,
  TbArrowBarToDown, TbBrandGithub,
} from "react-icons/tb";
import {
  GrDocumentPdf
} from "react-icons/gr"

import avatar from "../images/icon.png";
import { Application as SplineApplication } from "@splinetool/runtime";

import "../main.scss";

const profile_settings = {
  avatar,
  name: "Dmitriy Khanin",
  description: "I'm Front-end developer\nBased in Belarus",
};

const social_networks = [
  {
    icon: <TbBrandInstagram size="20" />,
    text: "Instagram",
    link: "https://www.instagram.com/dmkhanin/",
  },
  {
    icon: <TbBrandTelegram size="20" />,
    text: "Telegram",
    link: "https://t.me/dmkhanin",
  },
  {
    icon: <TbBrandLinkedin size="20" />,
    text: "LinkedIn",
    link: "https://www.linkedin.com/in/dmitriykhanin",
  },
  {
    icon: <TbBrandGithub size="20" />,
    text: "Github",
    link: "https://github.com/khanindev",
  },
];

import Spline from "@splinetool/react-spline";

const IndexPage: React.FC<PageProps> = () => {
  const canvasRef = React.useRef<HTMLCanvasElement | null>(null);

  React.useLayoutEffect(() => {
    if (canvasRef?.current) {
      const splineApp = new SplineApplication(canvasRef?.current);
      splineApp.load("https://prod.spline.design/mCEgkY8g7BLsjF28/scene.splinecode");
    }
  }, [canvasRef]);



  return (
    <React.Fragment>
      <div className="root">
        <div className="background">
          <canvas width={900} height={900} id="canvas" ref={canvasRef}></canvas>
        </div>
        <div className="main-container">
          <header>
            <h1 className={"name"}>{profile_settings.name}</h1>
            <p className={"description"}>{profile_settings.description}</p>
          </header>
          <main className="main">
            <address>
              <ul className="links">
                {social_networks.map((network) => (
                  <li key={network.text}>
                    <a className="link card" href={network.link} target="_blank">
                      <span className={"link-text"}>
                        {network.icon} {network.text}
                      </span>
                    </a>
                  </li>
                ))}
              </ul>
              <a className="link" download href={"/cv.pdf"} target="_blank">
                    <span className={"link-text"}>
                      <GrDocumentPdf className="pdf-icon" size={18} />
                      CV (Rus)
                    </span>
                <TbArrowBarToDown size={18} />
              </a>
            </address>
          </main>
          <footer className="footer">
            <span className="footer-text">
              Dmitriy Khanin &copy; {new Date().getFullYear()}
            </span>
          </footer>
        </div>
      </div>
    </React.Fragment>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Dmitriy Khanin</title>;
